<template>
  <div class="container404">
    <p>请按规定传递token初始化</p>
    <br>
    <p>测试直接输入：</p>
    <Input search enter-button="确定" placeholder=token style="width: 200px" v-model="token" @on-search="go"/>
  </div>
</template>
<script>
import { Button } from 'iview'

export default {
  name: 'Index',
  components: {Button},
  data () {
    return {
      token: ''
    }
  },
  mounted () {
    this.hideLoading()
  },
  methods: {
    hideLoading () {
      let loading = document.getElementById('loading')
      if (loading) {
        loading.classList.add('loading-hide')
        setTimeout(function () {
          loading.parentNode.removeChild(loading)
        }, 400)
      }
    },
    go () {
      window.location.href = '#/?token=' + this.token
    }
  }
}
</script>
<style lang='stylus' scoped>
  .container404
    width 100vw
    height 100vh
    display flex
    flex-direction column
    align-items center
    justify-content center
    & > img
      width 700px
</style>



